.container {
  display: flex;
  justify-content: space-between;
}

.colorWhite {
  color: black;

  background: white;

  &:hover {
    background: rgb(236 236 236);
  }
}

.colorBlue {
  color: white;

  background: #1b79ce;

  &:hover {
    background: #1668bd;
  }
}

.colorRed {
  color: white;

  background: rgb(234 0 0);

  &:hover {
    background: rgb(182 0 0);
  }
}
